<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process260"></a>

<p><br></p>
<p><span class="rvts17">一个工艺人的美梦与噩梦（</span><span class="rvts50">A Craftsman of Dreams and Nightmares</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:270,Y:308</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：玩具商桑德斯（</span><span class="rvts20">Toyseller Sanders</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">从桑德斯这里找到可以充能的护符，并让他告诉你如何度过鲜血之路</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1430.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在阿克斯广场中心处，可以找到一家玩具商店。和这里的老板桑德斯谈话，通过各种语言环境的检查，他会给你一个可以充能的护符。另外他还需要你首先知道阿虎的下落，之后才会告诉你如何度过鲜血之路。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1431.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">在成功解救了阿虎大人后（或与阿虎大人的灵魂交谈），和他对话，他会让你把护符充满源力（一共需要冲</span><span class="rvts20">5</span><span class="rvts21">次），之后会允许你上楼找到阿虎提到的那份</span><span class="rvts20"></span><span class="rvts21">赎罪卷轴</span><span class="rvts20"></span><span class="rvts21">，并告诉你打开柜子的密码。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1432.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">柜子在楼上的一处墙面处，密码为吉欧雅（</span><span class="rvts20">Giyora</span><span class="rvts21">），使用密码可以获得</span><span class="rvts20"></span><span class="rvts21">赎罪卷轴</span><span class="rvts20"></span><span class="rvts21">。有了赎罪卷轴和充能的护符，就可以开始主线任务</span><span class="rvts20"></span><span class="rvts21">大锤的陨落</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts21">其实，即便不救阿虎大人，强杀玩具店商人，也可以从他身上获得那件充能护符，之后上楼，也可以得到这份</span><span class="rvts20"></span><span class="rvts21">赎罪卷轴</span><span class="rvts20"></span><span class="rvts21">。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



